import DashboardLayout from "@/admin/layout/dashboard/DashboardLayout.vue";
// GeneralViews
import NotFound from "@/admin/pages/NotFoundPage.vue";

// Admin pages
import Dashboard from "@/admin/pages/Dashboard.vue";
import Notifications from "@/admin/pages/Notifications.vue";
import Icons from "@/admin/pages/Icons.vue";
import Expired from "@/admin/pages/Expired"
import TotalChemiscal from "@/admin/pages/TotalChemiscal"
import Statistics from "@/admin/pages/Statistics"
import ChemiscalImport from "@/admin/pages/ChemiscalImport"
import ChemiscalExport from "@/admin/pages/ChemiscalExport"
import MemberList from "@/admin/pages/MemberList.vue";
import Machine from "@/admin/pages/Machine.vue";
import Supplies from "@/admin/pages/Supplies.vue";
import Supplier from "@/admin/pages/Supplier.vue";
import Profile from "@/admin/pages/Profile.vue";
import System from "@/admin/pages/Login-Register/System"
import ExamineRequest from "@/admin/pages/Examine Request"
import Quota from "@/admin/pages/Quota"

// Client pages
import ClientLayout from "../client/components/ClientLayout"
import Home from "../client/pages/Home"

var isAuthenticated = false;
if (localStorage.getItem('token')) isAuthenticated = true;

const routes = [
  {
    path: "/admin",
    component: DashboardLayout,
    redirect: "/admin",
    children: [
      {
        path: "/admin",
        name: "Bảng điều khiển",
        component: Dashboard
      },
      {
        path: "notifications",
        name: "notifications",
        component: Notifications
      },
      {
        path: "don-vi",
        name: "Đơn vị",
        component: Icons
      },
      {
        path: "hoa-chat-het-han",
        name: "Hóa chất hết hạn",
        component: Expired
      },
      {
        path: "vat-tu-hoa-chat",
        name: "Vật tư hóa chất",
        component: TotalChemiscal
      },
      {
        path: "thong-ke-ton-kho",
        name: "thống kê tồn kho",
        component: Statistics
      },
      {
        path: "hoa-chat-nhap-kho",
        name: "hóa chất nhập kho",
        component: ChemiscalImport
      },
      {
        path: "hoa-chat-xuat-kho",
        name: "hóa chất xuất kho",
        component: ChemiscalExport

      },
      {
        path: "danh-sach-thanh-vien",
        name: "danh sách thành viên",
        component: MemberList
      },
      {
        path: "kho-may",
        name: "kho máy",
        component: Machine
      },
      {
        path: "vat-tu",
        name: "vật tư",
        component: Supplies
      },
      {
        path: "nha-cung-cap",
        name: "nhà cung cấp",
        component: Supplier
      },
      {
        path: "thong-tin-ca-nhan",
        name: "thông tin cá nhân",
        component: Profile
      },
      {
        path: "yeu-cau-xet-nghiem",
        name: "yêu cầu xét nghiệm",
        component: ExamineRequest
      },
      {
        path: "dinh-muc",
        name: "định mức",
        component: Quota
      },
    ]
  },
  {
    path: "/login",
    name: "Đăng nhập",
    component: System,
    beforeEnter: (to, from, next) => {
      if (to.name == 'Đăng nhập' && isAuthenticated) { 
        
        next({ name: 'Bảng điều khiển' })
      }
      next()
    },
  },
  { path: "*", component: NotFound },
  {
    path: "/",
    component: ClientLayout,
    redirect: "/",
    children: [
      {
        path: "/",
        name: "Trang chủ",
        component: Home
      },
    ]
  }
];

/**
 * Asynchronously load view (Webpack Lazy loading compatible)
 * The specified component must be inside the Views folder
 * @param  {string} name  the filename (basename) of the view to load.
function view(name) {
   var res= require('../components/Dashboard/Views/' + name + '.vue');
   return res;
};**/

export default routes;
